@import '../common/styles/sass/variables.scss';

.skillsBlock {
  min-height: 100vh;
  background-color: $bgSecondColor;

  .skillsContainer {
    flex-direction: column;
    justify-content: flex-start;

    .skills {
      width: 100%;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      box-sizing: border-box;
      margin-top: 20px;
    }
  }
}


